import FunctionalTable from "components/CustomFunctionalTable";
import ProfileAttributes from "components/ProfileAttributes";
import ProfileDescription from "components/ProfileDescription";
import ProfileDetails from "components/ProfileDetails";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { getAllDetails, getPharmacyAdminData } from "redux/Actions/Auth";
import { ROUTES, SNACK_VARIANT, STRINGS, USER_TYPES } from "shared/Constants";
import { Images } from "shared/Images";
import { getDocsUrl } from "shared/Utilities";
import { pharmacyAdminProfileDetails, pharmacyDocumentView } from "../constants";
import DocumentViewerModal from "components/DocumentViewer";


const imageURL =
    "https://images.unsplash.com/photo-1575936123452-b67c3203c357?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8aW1hZ2V8ZW58MHx8MHx8&w=1000&q=80";

const tableHeaderFunc = ["#", "Document Name", "Uploaded Document", "Status"];


const PharmacyProfile = () => {
    const history = useHistory()
    const dispatch = useDispatch()
    const [msg, sendNotifications] = useNotification();
    const profile = useSelector((state) => state.common.profileData);
    const [profileData, setProfileData] = useState([]);
    const [csData, setCSData] = useState([]);
    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [modalOpacity, setModalOpacity] = useState(false);

    const handleDocumentClick = (a, b) => {
        if (a.document) {
            setDocumentUrl(getDocsUrl(a.document));
            setModalOpacity(true);
            setViewDoc(true);
        }
    };

    const handleActionClick = () => {
        console.log("Action Clicked")
    }

    useEffect(() => {
        dispatch(
            getPharmacyAdminData(
                (response) => {
                    sendNotifications({
                        msg: response.data.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, []);

    console.log(profile, "profilelelle")

    useEffect(() => {
        setProfileData(pharmacyAdminProfileDetails(profile))
        setCSData(profile?.pharmacy_info?.csInUse || [])
    }, [profile]);

    return (
        <main className="main">
            {/* <Header /> */}

            <div className="inner_content view_profile_sec">

                <div className="d-flex justify-content-between">
                    <div className="title_h1 mb-4">
                        <h1 className="h6">View Profile</h1>
                    </div>
                    <button
                        className="btn btn-xs btn-primary"
                        onClick={() => {
                            history.push(ROUTES.MANAGE_PROFILE);
                        }}
                    >
                        <img src={Images.EditIcon} alt="" width={20} />
                    </button>
                </div>
                <ProfileDetails data={profileData} imageURL={getDocsUrl(profile?.owner_info?.photo)} verifiedName={profile.verified_document} profileCompleteness={profile.profile_completeness} rating={3} />
                <ProfileAttributes
                    title={"Computer Systems Familiar With:"}
                    attributes={csData}
                />
                <ProfileDescription
                    title={"Descriptions"}
                    description={profile?.owner_info?.description || ""}
                />
                <FunctionalTable
                    theadData={tableHeaderFunc}
                    tbodyData={pharmacyDocumentView(profile?.pharmacy_info?.documents)}
                    handleActionClick={handleActionClick}
                    handleDocumentClick={handleDocumentClick}
                    type={USER_TYPES.PHARMASY_ADMIN}
                />
            </div>
            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    documentUrl={documentUrl}
                    setViewDoc={setViewDoc}
                    modalOpacity={modalOpacity}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
        </main>
    );
};

export default PharmacyProfile;
